<template>
  <div id="contract_component">
        <div class="contract_component">
          <van-field
            class="middle"
            v-model="contract.article"
            autosize
            disabled
            type="textarea"
          />
          <div class="button">
            <ul>
              <li>
                <nav>For and on behalf of:</nav>
                <h3>{{contract.owner_behalf}}</h3>
              </li>
              <li>
                <p><img v-if="contract.owner_sign" :src="contract.owner_sign"alt=""></p>
                <p>Signature</p>
              </li>
              <li>
                <p><span>{{contract.owner_name}}</span></p>
                <p>Name</p>
              </li>
              <li>
                <p><span>{{contract.owner_title}}</span></p>
                <p>Title</p>
              </li>
              <li>
                <p><span>{{owner_signdate}}</span></p>
                <p>Date</p>
              </li>
            </ul>
            <ul>
              <li>
                <nav>For and on behalf of:</nav>
                <h3>{{contract.agent_behalf}}</h3>
              </li>
              <li>
                <p><img v-if="contract.agent_sign"  :src="contract.agent_sign"alt=""></p>
                <p>Signature</p>
              </li>
              <li>
                <p><span>{{contract.agent_name}}</span></p>
                <p>Name</p>
              </li>
              <li>
                <p><span>{{contract.agent_title}}</span></p>
                <p>Title</p>
              </li>
              <li>
                <p><span>{{agent_signdate}}</span></p>
                <p>Date</p>
              </li>
            </ul>
          </div>
        </div>
  </div>
</template>
<script>
  export default {
    name: "contract_component",
    props:['contract'],
    data() {
      return {
        // contract:{
        //   article:'',
        //   owner_sign:'',
        //   owner_name:'',
        //   owner_title:'',
        //   owner_signdate:null,
        //   agent_name:'',
        //   agent_title:'',
        //   agent_signdate:null,
        // }
      };
    },
    computed:{
      owner_signdate:function () {
        if(this.contract.owner_signdate!=null){
          return    this.$global.stamptodate(this.contract.owner_signdate)
        }
      },
      agent_signdate:function () {
        if(this.contract.agent_signdate!=null){
          return   this.$global.stamptodate(this.contract.agent_signdate)
        }
      }
    },
    created() {
    },
    mounted() {
    },
    methods: {
    }
  };
</script>
<style lang="scss">
  #contract_component{
    padding: 0.4rem 0.4rem;
    border: 1px solid #b5b5b5;
    box-sizing: border-box;
    .middle{
      padding: 0;
    }
      .contract_component {
        font-size: 0.38rem;
        line-height: 0.6rem;
        width: 100%;
        height: 12rem;
        overflow-y: auto;
        word-wrap: break-word;
        div.button {
          margin-top: 1rem;
          display: flex;
          justify-content: space-between;
          ul{
            width: 3rem;
            li{
              /*position: relative;*/
              p{
                height: 1rem;
                position: relative;
                img {
                  width: 3rem;
                  height: 1rem;
                }
                span{
                  position: absolute;
                  bottom: 0;
                }
              }
              p:nth-child(1){
                /*position: absolute;*/
                /*bottom: 0;*/
                /*line-height:1rem;*/
                border-bottom: 1px solid;
              }
            }
            li:nth-of-type(1){
              /*margin-bottom: 1rem;*/
              nav{
                line-height: initial;

              }
              h3{
                /*width: 3rem;*/
                height: 3.6rem;
              }
            }
          }
        }
      }
      footer {
        width: 100%;
        font-size: 0.38rem;
        button {
          width: 100%;
          margin-top: 1rem;
          background: #00adef;
          color: white;
          height: 1rem;
        }
      }

  }

</style>



